<template>
  <div class="catalog">
    <div
      class="catalog_content"
      :class="page == 'personal' ? 'catalog_choose' : ''"
      @click="toOther('personal')"
    >
      个人资料
    </div>
    <div
      class="catalog_content"
      :class="page == 'task' ? 'catalog_choose' : ''"
      @click="toOther('task')"
    >
      任务进程
    </div>
    <div
      class="catalog_content"
      :class="page == 'manage' ? 'catalog_choose' : ''"
      @mousemove="showBar('re')"
      @mouseleave="hideBar('re')"
    >
      <div class="content_main" @click="toOther('manageal')">注册任务管理</div>
      <!-- <div class="other" :class="showRe ? 'show_oter' : ''">
        <div class="other_content" @click="toOther('manage')">待入库任务</div>
        <div class="other_content" @click="toOther('manageal')">已入库任务</div>
      </div> -->
    </div>
    <div
      class="catalog_content"
      :class="page == 'distribute' ? 'catalog_choose' : ''"
      @mousemove="showBar('se')"
      @mouseleave="hideBar('se')"
    >
      <div class="content_main" @click="toOther('distributeal')">
        分发任务管理
      </div>
      <!-- <div class="other" :class="showSe ? 'show_oter' : ''">
        <div class="other_content" @click="toOther('distribute')">
          待出库任务
        </div>
        <div class="other_content" @click="toOther('distributeal')">
          已出库任务
        </div>
      </div> -->
    </div>
    <div
      class="catalog_content"
      :class="page == 'cart' ? 'catalog_choose' : ''"
      @click="toOther('cart')"
    >
      购物车(5)
    </div>
  </div>
</template>
<script>
export default {
  props: {
    page: "",
  },
  data() {
    return {
      showRe: false,
      showSe: false,
    };
  },
  methods: {
    toOther(key) {
      switch (key) {
        case "personal":
          this.$router.push({
            path: "/mine/personal",
          });
          break;

        case "task":
          this.$router.push({
            path: "/mine/task",
          });
          break;

        case "manage":
          this.$router.push({
            path: "/mine/manage",
          });
          break;

        case "manageal":
          this.$router.push({
            path: "/mine/manageal",
          });
          break;

        case "cart":
          this.$router.push({
            path: "/mine/cart",
          });
          break;
        case "distribute":
          this.$router.push({
            path: "/mine/distribute",
          });
          break;
        case "distributeal":
          this.$router.push({
            path: "/mine/distributeal",
          });
          break;
        default:
          this.$router.push({
            path: "/mine/personal",
          });
          break;
      }
    },
    showBar(key) {
      if (key == "re") {
        this.showRe = true;
      }
      if (key == "se") {
        this.showSe = true;
      }
    },
    hideBar(key) {
      if (key == "re") {
        this.showRe = false;
      }
      if (key == "se") {
        this.showSe = false;
      }
    },
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.catalog {
  width: 256px;
  border-right: 2px solid rgba(225, 225, 225, 0.5);
  height: 500px;

  .catalog_content {
    width: 232px;
    height: 40px;
    margin: 24px 12px 0;
    line-height: 40px;
    font-size: 18px;
    color: #333333;
    text-align: center;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    justify-content: center;
    position: relative;
    .content_main {
      width: 232px;
      height: 40px;
    }
    .other {
      position: absolute;
      z-index: 1;
      left: 232px;
      width: 232px;
      border: 1px solid #efefef;
      display: none;
      .other_content {
        background: #ffffff;
        color: #333;
      }
      .other_content:hover {
        background: #0695d4;
        color: #ffffff;
      }
    }
    .show_oter {
      display: block;
    }
  }
  .catalog_choose {
    background: #e5f7ff;
    border-radius: 2px;
    color: #0695d4;
  }
}
</style>


