<template>
  <div class="product_list">
    <div class="product_main">
      <cata-log :page="page"></cata-log>
      <div class="manage">
        <div class="search">
          <div class="search_in">
            <div class="info_name">任务类型：</div>
            <div class="select">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="search_in">
            <div class="info_name">项目：</div>
            <input class="info_input" type="text" placeholder="请输入" />
          </div>
          <div class="search_in">
            <div class="info_name">是否投递：</div>
            <div class="select">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="search_in"><div class="btn">查&nbsp;询</div></div>
        </div>
        <div class="task">
          <div class="task_tab">
            <div class="tab_show tab_one">任务号</div>
            <div class="tab_show tab_two">项目</div>
            <div class="tab_show tab_one">任务类型</div>
            <div class="tab_show tab_one">审核确认状态</div>
            <div class="tab_show tab_one">保存培养协议</div>
            <div class="tab_show tab_three">是否投递</div>
            <div class="tab_show tab_three">操作</div>
          </div>
          <div class="task_content" v-for="(item, index) of 14" :key="index">
            <div class="content_show content_one">20200410</div>
            <div class="content_show content_two">
              这是一段描述，关于这个应用的描述文字这是一段描述，关于这个应用的描述文字内
            </div>
            <div class="content_show content_one">细胞保存培养</div>
            <div class="content_show content_one content_modify">待修改</div>
            <div class="content_show content_one" v-if="index % 5 == 0">
              <span class="dot dot_red"></span> 未上传
            </div>
            <div class="content_show content_one" v-else-if="index % 3 == 0" >
              <span class="dot dot_blue"></span> 待上传
            </div>
            <div class="content_show content_one" v-else>
              <span class="dot"></span> 已上传
            </div>
            <div class="content_show content_three">未递送</div>
            <div class="content_show content_three content_modify">撤回</div>
          </div>
        </div>
        <div class="paginaton">
          <el-pagination
            :background="false"
            :pager-count="9"
            layout="prev, pager, next"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="80"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import CataLog from "/page/mine/catalog";
export default {
  data() {
    return {
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
      value: "",
      date: "",
      page: "manage",
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this._getSearch();
      this.loading = true;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this._getSearch();
      this.loading = true;
    },
    toOther(key) {
      switch (key) {
        case "personal":
          this.$router.push({
            path: "/mine/personal",
          });
          break;

        case "task":
          this.$router.push({
            path: "/mine/task",
          });
          break;

        case "manage":
          this.$router.push({
            path: "/mine/manage",
          });
          break;

        case "cart":
          this.$router.push({
            path: "/mine/cart",
          });
          break;
        default:
          this.$router.push({
            path: "/mine/personal",
          });
          break;
      }
    },
  },
  created() {},
  components: {
    CataLog,
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f0faff;
  width: 1440px;
  margin: 0 auto;
  padding-top: 24px;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
  display: flex;
  .manage {
    width: 946px;
    .search {
      padding: 24px;
      display: flex;
      flex-wrap: wrap;
      .search_in {
        display: flex;
        margin-top: 24px;
        margin-left: 24px;
      }
      .info_name {
        font-size: 14px;
        color: rgba(0, 0, 0, 0.85);
        line-height: 40px;
        width: 80px;
      }
      .info_input {
        width: 216px;
        height: 40px;
        background: #ffffff;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 2px;
        font-size: 14px;
        line-height: 40px;
        padding-left: 12px;
        margin-right: 24px;
      }
      .select {
        width: 216px;
        height: 40px;
      }
      .btn {
        background: #0695d4;
        border-radius: 2px;
        border-radius: 2px;
        height: 38px;
        width: 72px;
        text-align: center;
        line-height: 40px;
        font-size: 14px;
        color: #ffffff;
        cursor: pointer;
      }
    }
    .task {
      padding: 0 24px;
      width: 946px;
      .task_tab {
        display: flex;
        background: #fafafa;
        border-radius: 4px;
        width: 898px;
        height: 54px;
        border-bottom: 1px solid #e8e8e8;
        .tab_show {
          font-size: 14px;
          color: rgba(0, 0, 0, 0.85);
          height: 54px;
          line-height: 54px;
          font-weight: bold;
          text-align: center;
        }
        .tab_one {
          width: 120px;
        }
        .tab_two {
          width: 200px;
        }
        .tab_three {
          width: 100px;
        }
      }
      .task_content {
        display: flex;
        height: 75px;
        border: 1px solid #e8e8e8;
        border-top: none;
        .content_show {
          font-size: 14px;
          color: rgba(0, 0, 0, 0.65);
          text-align: center;
          height: 75px;
          line-height: 75px;
        }
        .content_one {
          width: 120px;
          display: flex;
          justify-content: center;
        }
        .content_two {
          width: 200px;
          line-height: 22px;
          height: 44px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          margin-top: 16px;
        }
        .content_three {
          width: 100px;
        }
        .content_modify {
          color: #0695d4;
        }
        .dot {
          width: 6px;
          height: 6px;
          border-radius: 3px;
          background: rgba(0, 0, 0, 0.25);
          position: relative;
          top: 34px;
          right: 6px;
        }
        .dot_blue {
          background: #0695d4;
        }
        .dot_red {
          background: #f5222d;
        }
      }
    }
  }
}
</style>



